<template>
  <list-page :title="$t('settings.account.title')">
    <cell
      :label="emailLabel"
      :value="email"
      vertical
      use-icon
      @click="toEmail"
    />
    <cell
      :label="phoneLabel"
      :value="phoneValue"
      vertical
      use-icon
      @click="toPhone"
    />
    <cell
      :label="$t('settings.account.pwd.title')"
      use-icon
      @click="toChangePwd"
    />
    <cell
      :label="$t('settings.account.signOut.title')"
      @click="signOut"
    />
  </list-page>
</template>

<script>
import { mapState } from 'vuex'
import ListPage from '@/components/list/ListPage.vue'
import Cell from '@/components/list/Cell.vue'

export default {
  name: 'SettingsAccount',
  components: {
    ListPage,
    Cell
  },
  computed: {
    ...mapState(['user']),
    email () {
      return this.user ? this.user.email : ''
    },
    emailLabel () {
      return this.email ? this.$t('settings.account.email.has') : this.$t('settings.account.email.empty')
    },
    hasPhone () {
      let user = this.user
      return user && user.areaCode && user.phone
    },
    phoneValue () {
      return this.hasPhone ? `${ this.user.areaCode } ${ this.user.phone }` : ''
    },
    phoneLabel () {
      return this.hasPhone ? this.$t('settings.account.phone.has') : this.$t('settings.account.phone.empty')
    }
  },
  methods: {
    toEmail () {
      this.$router.push({
        name: 'settings-account-email'
      })
    },
    toPhone () {
      this.$router.push({
        name: 'settings-account-phone'
      })
    },
    toChangePwd () {
      this.$router.push({
        name: 'settings-account-cpwd'
      })
    },
    signOut () {
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
