<template>
  <div class="l-page">
    <div class="l-page__header--primary">
      <div
        v-if="miniSize"
        class="l-flex__fill l-flex--row l-row-layout"
      >
        <img class="o-avatar l-flex__none" :src="avatarUrl" @click="$features.sign()"/>
        <div class="l-flex__fill o-nick is-ellipsis">{{ msg }}</div>
      </div>
      <div
        v-else
        class="l-page__title"
      >{{ $t('tabbar.account') }}</div>
      <div class="l-flex__none l-flex--row">
        <router-link
          tag="i"
          class="o-nav-icon"
          :to="{ name: 'mail' }">
          <img class="o-nav-icon" src="@/assets/icon-notification.png"/>
        </router-link>
        <router-link
          tag="i"
          class="o-nav-icon"
          :to="{ name: 'settings' }">
          <img class="o-nav-icon" src="@/assets/icon-settings.png"/>
        </router-link>
      </div>
    </div>
    <div ref="content" class="l-page__content l-flex--column">
      <div v-if="!miniSize" class="l-flex__none l-column-layout o-account">
        <img class="o-avatar" :src="avatarUrl" @click="$features.sign()"/>
        <div class="o-nick is-ellipsis">{{ msg }}</div>
      </div>
      <div class="l-flex__fill l-flex--column">
        <div class="l-flex--row has-shadow--short u-background--blue">
          <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 0 }" @click="switchTab(0)">{{ $t('mine.journey') }}</div>
          <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 1 }" @click="switchTab(1)">{{ $t('mine.orders') }}</div>
          <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 2 }" @click="switchTab(2)">{{ $t('mine.coupon') }}</div>
        </div>
        <div class="l-hscroll l-flex__fill">
          <div class="l-hscroll__wrapper" :class="{ 'is-second': tabIndex === 1, 'is-third': tabIndex === 2 }">
            <div
              ref="ticket"
              class="l-hscroll__item"
              @scroll="scrollTicket"
            >
              <div v-if="hasTickets" class="has-padding--v">
                <indicator
                  v-if="tickets.length > 1"
                  class="c-vlist-item"
                  :count="tickets.length"
                  :current="currentTicketIndex"
                />
                <van-swipe
                  ref="swiper"
                  class="c-vlist-item"
                  :initial-swipe="ticketIndex"
                  :show-indicators="false"
                  :loop="false"
                  @change="changeTicket"
                >
                  <van-swipe-item
                    v-for="(ticket, index) in tickets"
                    :key="index"
                    :style="{ height: `${ swiperHeight }px` }"
                  >
                    <div
                      :ref="`ticket${ index }`"
                      class="has-padding--h"
                    >
                      <ticket
                        :ticket="ticket"
                        @toggle="checkTickets(true)"
                      />
                    </div>
                  </van-swipe-item>
                </van-swipe>
              </div>
              <div v-else class="c-empty is-fill">
                <img class="c-empty__img" src="~@/assets/img-haitun.png"/>
                <div class="c-empty__tip">{{ $t('mine.noticket') }}</div>
                <div class="c-empty__footer">
                  <router-link tag="div" class="c-empty__button" :to="{ name: 'buy-ticket' }">{{ $t('btn.buyticket') }}</router-link>
                  <router-link tag="div" class="c-empty__button" :to="{ name: 'bind-ticket' }">{{ $t('btn.bindticket') }}</router-link>
                </div>
              </div>
            </div>
            <div
              ref="order"
              class="l-hscroll__item"
              @scroll="scrollOrder"
            >
              <div class="c-types">
                <div class="c-types__item" :class="{ 'is-selected': orderType === 'all' }" @click="changeType('all')">{{ $t('mine.types.all') }}</div>
                <div class="c-types__item" :class="{ 'is-selected': orderType === 'paid' }" @click="changeType('paid')">{{ $t('mine.types.paid') }}</div>
                <div class="c-types__item" :class="{ 'is-selected': orderType === 'unpaid' }" @click="changeType('unpaid')">{{ $t('mine.types.unpaid') }}</div>
                <div class="c-types__item" :class="{ 'is-selected': orderType === 'cancelled' }" @click="changeType('cancelled')">{{ $t('mine.types.cancelled') }}</div>
              </div>
              <order
                v-for="(order, index) in orderList"
                :key="index"
                class="u-separate--nearer u-background--white"
                :order="order"
                detail
                @del="toDel(order)"
              />
            </div>
            <div class="l-hscroll__item">
              Coupons
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { dateFormat, transformTicketValidTime } from '@/utils/format.js'
import { sortBySortIndex } from '@/utils/sort.js'
import { Icon, Swipe, SwipeItem } from 'vant'
import Indicator from '@/components/Indicator.vue'
import Order from '@/components/Order.vue'
import Ticket from '@/components/mine/Ticket.vue'

export default {
  name: 'Account',
  components: {
    [ Swipe.name ]: Swipe,
    [ SwipeItem.name ]: SwipeItem,
    Indicator,
    Order,
    Ticket
  },
  data () {
    return {
      localeKey: '',
      tabIndex: 0,
      swiperHeight: 0,
      tickets: [],
      ticketIndex: 0,
      currentTicketIndex: 0,
      ticketScrollOut: false,
      orderScrollOut: false,
      orders: [],
      orderType: 'all'
    }
  },
  computed: {
    ...mapGetters(['isAuth', 'isAbnormal', 'isLogined', 'nickName', 'avatarUrl', 'hasParkMails']),
    hasTickets () {
      return this.tickets.length > 0
    },
    msg () {
      if (!this.isAuth || this.isAbnormal) {
        return this.$t('sign.title')
      }
      return this.nickName
    },
    miniSize () {
      return this.tabIndex === 0 ? this.ticketScrollOut : this.orderScrollOut
    },
    orderList () {
      switch (this.orderType) {
        case 'paid':
          return this.orders.filter(order => order.status > 37)
        case 'unpaid':
          return this.orders.filter(order => order.status === 1)
        case 'cancelled':
          return this.orders.filter(order => order.status === 2)
        default:
          return this.orders
      }
    }
  },
  methods: {
    changeTicket (index) {
      this.currentTicketIndex = index
      this.checkTickets(true)
    },
    getTickets () {
      if (this.$ticketFresh === 1) {
        return
      }

      clearTimeout(this.$ticketTimer)
      this.$ticketFresh = 1

      let today = new Date(dateFormat(Date.now(), 'yyyy/MM/dd')).getTime()
      this.$http.post('/api/common/user/ticket/list', null, {
        login: true
      }).then(data => {
        if (this.$ticketFresh >= 1) {
          this.tickets = data.data.sort(sortBySortIndex)

          let index = Math.max(0, Math.min(this.currentTicketIndex, this.tickets.length - 1))
          if (this.currentTicketIndex !== index) {
            this.currentTicketIndex = index
            this.ticketIndex = index
          }

          this.$ticketHeight = 0
          if (this.$ticketFresh === 1) {
            this.checkTickets()
          }

          this.$ticketFresh = 99
        }
      }).finally(() => {
        if (this.$ticketFresh <= 1) {
          this.$ticketFresh = 0
          this.$ticketTimer = setTimeout(this.getTickets, 2000)
        }
      })
    },
    checkTickets (force) {
      if (this.tickets.length === 0) {
        this.$ticketCanScroll = false
        this.ticketScrollOut = false
        this.swiperHeight = 0
      } else {
        if (!force && this.$ticketHeight) {
          if (this.$ticketScrollTop) {
            this.$refs.ticket.scrollTop = this.$ticketScrollTop
            this.$ticketScrollTop = 0
          }
        } else {
          this.$nextTick(() => {
            let swiperHeight = this.$refs[`ticket${ this.currentTicketIndex }`][0].offsetHeight
            if (swiperHeight !== this.swiperHeight) {
              this.swiperHeight = swiperHeight
              this.$nextTick(() => {
                this.$ticketHeight = this.$refs.ticket.scrollHeight
                this.$ticketCanScroll = this.$ticketHeight >= this.$contentHeight + 40
                this.ticketScrollOut = this.$ticketCanScroll && (this.$refs.ticket.scrollTop > 40)
              })
            }
          })
        }
      }
    },
    scrollTicket (e) {
      if (this.$ticketCanScroll) {
        this.ticketScrollOut = e.target.scrollTop > 40
      }
    },
    transformOrder (order) {
      let { orderId: id, addTime, status, coupon, spOrderList, totalPrice: total } = order

      return {
        info: {
          id, date: dateFormat(addTime, 'yyyy.MM.dd')
        },
        status, coupon, total,
        list: spOrderList[0].productList.map(ticket => {
          return {
            name: ticket.productName,
            count: ticket.productCount,
            total: ticket.price,
            date: transformTicketValidTime(ticket.extInfo)
          }
        }),
        createTime: addTime
      }
    },
    toDel (order) {
      let orderId = order.info.id
      let index = this.orders.findIndex(order => order.info.id === orderId)
      if (index > -1) {
        this.orders.splice(index, 1)
      }
    },
    getOrders () {
      if (this.$orderFresh === 1) {
        return
      }

      this.$orderFresh = 1

      this.$http.post('/api/common/user/order/list', null, {
        login: true
      }).then(data => {
        if (this.$orderFresh >= 1) {
          this.orders = data.data.map(this.transformOrder)

          this.$orderHeight = 0
          if (this.$orderFresh === 1) {
            this.checkOrders()
          }

          this.$orderFresh = 99
        }
      }).finally(() => {
        if (this.$orderFresh <= 1) {
          this.$orderFresh = 0
          this.$orderTimer = setTimeout(this.getOrders, 2000)
        }
      })
    },
    checkOrders (force = false) {
      if (this.orderList.length < 2) {
        this.orderScrollOut = false
      } else {
        if (!force && this.$orderHeight) {
          if (this.$orderScrollTop) {
            this.$refs.order.scrollTop = this.$orderScrollTop
            this.$orderScrollTop = 0
          }
        } else {
          this.$nextTick(() => {
            this.$orderHeight = this.$refs.order.scrollHeight
            this.$orderCanScroll = this.$orderHeight >= this.$contentHeight + 40
            this.orderScrollOut = this.$orderCanScroll && (this.$refs.order.scrollTop > 40)
          })
        }
      }
    },
    scrollOrder (e) {
      if (this.$orderCanScroll) {
        this.orderScrollOut = e.target.scrollTop > 40
      }
    },
    changeType (type) {
      this.orderType = type
      this.checkOrders(true)
    },
    switchTab (index) {
      this.tabIndex = index
    },
    fetchList () {
      if (this.isLogined) {
        this.$ticketFresh = 0
        this.$orderFresh = 0
        this.getTickets()
        this.getOrders()
      } else {
        this.$ticketFresh = 99
        this.$orderFresh = 99
        this.tickets = []
        this.ticketIndex = 0
        this.currentTicketIndex = 0
        this.orders = []
      }
    }
  },
  created () {
    this.$ticketTimer = -1
    this.$orderTimer = -1
    this.$ticketFresh = 99
    this.$orderFresh = 99
  },
  mounted () {
    this.$contentHeight = this.$refs.content.offsetHeight
  },
  activated () {
    this.fetchList()
    this.checkTickets()
    this.checkOrders()
  },
  deactivated () {
    this.$ticketFresh = 99
    this.$orderFresh = 99
    this.$ticketScrollTop = this.$refs.ticket.scrollTop
    this.$orderScrollTop = this.$refs.order.scrollTop
    clearTimeout(this.$ticketTimer)
    clearTimeout(this.$orderTimer)
  }
}
</script>

<style lang="scss" scoped>
.l-page {
  background-color: $color-blue;
}

.l-hscroll {
  background-color: $color-separate--block;
}

.o-avatar {
  box-sizing: border-box;
  display: inline-block;
  border-radius: 50%;
  overflow: hidden;
  background: url(~@/assets/avatar.png) 0 0 / 100% 100% no-repeat;
}

.o-nick {
  @include is-title($color-white);
}

.l-column-layout {
  padding: 20px 40px 30px;
  text-align: center;

  .o-avatar {
    width: 158px;
    height: 158px;
    margin-top: 20px;
  }

  .o-nick {
    margin-top: 28px;
  }
}

.l-row-layout {
  .o-avatar {
    width: 60px;
    height: 60px;
    border: 2px solid transparent;
    background-color: $color-white;
  }

  .o-nick {
    margin-left: 18px;
  }
}

.c-types {
  padding: 32px 40px;
  white-space: nowrap;
  overflow-x: auto;
  background-color: $color-white;

  &__item {
    box-sizing: border-box;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    padding: 0 32px;
    font-size: 28px;
    color: $color;
    line-height: 34px;
    border: 2px solid $color-separate;
    border-radius: 32px;

    &.is-selected {
      color: $color-blue;
      border-color: transparent;
      background-color: rgba($color-blue, .1);
    }

    & + & {
      margin-left: 16px;
    }
  }
}
</style>
