<i18n>
{
  "en_US": {
    "phoneNumber": "Phone Number",
    "cancel": "Cancel",
    "confirm": "Confirm"
  },
  "zh_HK": {
    "phoneNumber": "手機號",
    "cancel": "取消",
    "confirm": "確認"
  },
  "zh_CN": {
    "phoneNumber": "手机号",
    "cancel": "取消",
    "confirm": "确认"
  }
}
</i18n>

<template>
  <div
    class="o-phone"
    :class="{ 'is-error': isError }"
  >
    <div class="o-phone__wrapper">
      <div class="o-phone__wrapper" @click="showAreaCodePicker">
        <div
          class="o-phone__code"
          :class="{ 'is-invalid': !areaCode }"
        >{{ codeMsg }}</div>
        <van-loading
          v-if="loading"
          type="spinner"
          class="o-phone__icon"
        />
        <van-icon
          v-else
          class="o-phone__icon"
          :name="icon"
        />
      </div>
      <div class="o-phone__separator"></div>
      <input
        class="o-phone__number"
        type="number"
        :placeholder="$t('phoneNumber')"
        :value="value"
        @input="onInput"
      />
    </div>
    <div class="o-phone__line"></div>
    <div class="o-phone__note">{{ noteMsg }}</div>
    <van-popup
      position="bottom"
      v-model="showPicker"
    >
      <van-picker
        :columns="columns"
        value-key="label"
        :default-index="areaCodeIndex"
        show-toolbar
        :confirm-button-text="$t('confirm')"
        :cancel-button-text="$t('cancel')"
        @cancel="showPicker = false"
        @confirm="onAreaCodeConfirm"
      />
    </van-popup>
  </div>
</template>

<script>
import Vue from 'vue'
import { Icon, Loading, Popup, Picker } from 'vant'

const state = Vue.observable({
  locale: '',
  loading: false,
  list: []
})

export default {
  name: 'Phone',
  components: {
    [ Icon.name ]: Icon,
    [ Loading.name ]: Loading,
    [ Popup.name ]: Popup,
    [ Picker.name ]: Picker
  },
  props: {
    areaCode: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    note: {
      type: String,
      default: ''
    },
    errMsg: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showPicker: false
    }
  },
  computed: {
    icon () {
      return 'arrow-down'
    },
    isError () {
      return !!this.errMsg
    },
    noteMsg () {
      return this.errMsg || this.note
    },
    loading () {
      return state.list.length === 0
    },
    areaCodeIndex () {
      return state.list.findIndex(region => region.code === this.areaCode)
    },
    codeMsg () {
      let region
      if (this.areaCodeIndex >= 0) {
        region = state.list[this.areaCodeIndex]
      } else {
        if (this.loading) {
          return this.areaCode
        }
        region = state.list[0]
      }
      return `${ region.name } ${ region.code }`
    },
    columns () {
      return state.list.map(region => {
        return {
          label: `${ region.name } ${ region.code }`,
          value: region.code
        }
      })
    }
  },
  methods: {
    load () {
      if (!state.loading && state.list.length === 0 || state.locale !== this.$i18n.locale) {
        state.locale = this.$i18n.locale
        state.loading = true
        this.$http.post('/app/common/areaList').then(data => {
          console.log(data)
          state.loading = false
          state.list = [{
            name: 'CN',
            code: '+86'
          }, {
            name: 'HK',
            code: '+852'
          }]
        })
      }
    },
    showAreaCodePicker () {
      if (this.loading) {
        this.load()
      } else {
        this.showPicker = true
      }
    },
    onAreaCodeConfirm (areaCode) {
      this.showPicker = false
      if (areaCode.value !== this.areaCode) {
        this.$emit('change', areaCode.value)
      }
    },
    onInput (e) {
      this.$emit('input', e.target.value)
    }
  },
  created () {
    this.load()
  }
}
</script>

