import '@/scss/index.scss'
import 'amfe-flexible'

import Vue from 'vue'

import router from '@/router'
import store from '@/store'
import i18n from '@/i18n'

import App from '@/App.vue'

import '@/globals.js'

import cfg from 'config'

import http from '@/utils/http.js'
import { log, setEnabled } from '@/utils/log.js'
import EventBus from '@/utils/event-bus.js'
import JsNativeApi, { inject } from '@/utils/js-native-api.js'
import { isExpired, authNomral } from '@/utils/auth.js'
import { constant } from '@/utils/common.js'
import features from '@/utils/features.js'

Vue.config.productionTip = cfg.isDebug
Vue.config.devtools = cfg.isDebug

if (cfg.isDebug) {
  Vue.config.warnHandler = function (msg, vm, trace) {
    console.log(msg, trace)
  }
  Vue.config.errorHandler = function (err, vm, info) {
    console.log(err, info)
  }
  Vue.prototype.$log = window.console.log
} else {
  setEnabled(cfg.useLog)
  Vue.prototype.$log = log
}

Vue.prototype.$eventBus = EventBus
Vue.prototype.$http = http
Vue.prototype.$native = JsNativeApi
Vue.prototype.$features = features

Promise.all([
  inject(),
  new Promise(resolve => {
    if (isExpired()) {
      authNomral(resolve)
    } else {
      authNomral()
      resolve()
    }
  })
]).then(() => {
  constant()
  new Vue({
    store,
    router,
    i18n,
    render: h => h(App)
  }).$mount('#app')
})
