<template>
  <div class="u-inline-block">
    <template v-if="url">
      <video
        v-if="isVideo"
        class="is-cover"
        :src="url"
         @play="play" @pause="pause" @error="error" @ended="ended"
        controls
        ></video>
      <img v-else class="is-cover" :src="url"/>
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: {
    info: {
      type: Object,
      default: null
    }
  },
  computed: {
    ...mapGetters(['baseUrl']),
    url () {
      if (this.info) {
        if (this.info.iconCos) {
          return this.info.iconCos
        }
        if (this.info.url && this.baseUrl) {
          return `${ this.baseUrl }${ this.info.url }`
        }
      }
      return null
    },
    isVideo () {
      return this.info && this.info.resourceType === 2
    }
  },
  methods: {
    play () {
      console.log('play')
      this.$emit('play')
    },
    pause () {
      console.log('pause')
      this.$emit('pause')
    },
    error () {
      console.log('error')
      this.$emit('error')
    },
    ended () {
      console.log('ended')
      this.$emit('ended')
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
