import axios from 'axios'
import i18n, { getLanguageKey } from '@/i18n/index.js'
import store from '@/store/index.js'
import { isExpired } from './auth.js'
import features from './features.js'
import cfg from 'config'
import { Toast } from 'vant'

axios.defaults.baseURL = cfg.baseURL
axios.defaults.timeout = cfg.timeout

function check (url, options) {
  let {
    loading = false, server = false,
    auth = true, login = false,
    fail = null
  } = options

  if (auth && isExpired()) {
    if (server) {
      features.alert(i18n.t('errMsg.auth'), fail)
    }
    console.log(url, 'Auth required, but not')
    return false
  }

  if (login && !store.getters.isLogined) {
    if (server) {
      features.alert(i18n.t('errMsg.login'), fail)
    }
    console.log(url, 'Login required, but current state is not logined')
    return false
  }

  if (loading !== false) {
    Toast.loading({
      forbidClick: true,
      duration: 0
    })
  }

  return true
}

function getHeader (type, options) {
  let {
    auth = true
  } = options

  let header = {
    'content-type': type || 'application/json'
  }

  if (auth) {
    header['optoken'] = store.state.authInfo.token
  }

  return header
}

function getCommonData (options) {
  let {
    login = false
  } = options

  let commonData = {
    pId: 1,
    lang: getLanguageKey()
  }

  if (login) {
    commonData['userIdType'] = store.state.authInfo.userIdType
    commonData['userId'] = store.state.authInfo.userId
  }

  return commonData
}

function createHttpReject (res, options = {}) {
  let {
    server = false, fail = null
  } = options

  if (server) {
    features.alert(i18n.t('errMsg.net', {
      code: res.statusCode
    }), fail)
  }

  return {
    system: true,
    code: -9999,
    msg: res.data.error || res.data.message,
    statusCode: res.statusCode
  }
}

function createSystemReject (res, options = {}) {
  let {
    server = false, fail = null
  } = options

  if (server) {
    features.alert(i18n.t('errMsg.sys'), fail)
  }

  return {
    system: true,
    code: -999,
    msg: res.message
  }
}

// loading 显示loading图标
// pulldown 下拉请求
// server 显示服务器异常原因弹出框（必须有返回值）
// auth 需授权后才能调用
// fail 弹框隐藏回调，modal为true时有效
// abort 接收task，根据isDone判断是否已完成
const http = {
  post (url, data = {}, options = {}) {
    if (!check(url, options)) {
      if (options.pulldown) {
        // uni.stopPullDownRefresh()
      }

      return Promise.reject({
        code: -10000
      })
    }

    let {
      loading = false, pulldown = false, server = false,
      fail = null, abort = null
    } = options

    return new Promise((resolve, reject) => {
      axios.post(url, {
        ...getCommonData(options),
        ...data
      }, {
        headers: getHeader('application/json', options)
      }).then(res => {
        if (loading !== false) {
          Toast.clear()
        }

        if (res.status === 200) {
          if (res.data.code === 0) {
            resolve(res.data.data)
          } else {
            console.log('request abnormal', url, res.data.code, res.data.msg)
            if (server && res.data.msg) {
              features.alert(res.data.msg, fail)
            }
            reject(res.data)
          }
        } else {
          console.log('request error', url, res.statusCode)
          reject(createHttpReject(res, options))
        }

        if (pulldown) {
          uni.stopPullDownRefresh()
        }
      }, err => {
        console.log('request fail', url, err.message)
        if (loading !== false) {
          Toast.clear()
        }

        reject(createSystemReject(err, err.message && /abort/.test(err.message) ? {} : options))

        if (pulldown) {
          uni.stopPullDownRefresh()
        }
      })
    })
  },
  upload (url, filePath, data = {}, options = {}) {
    if (!check(url, options)) {
      return Promise.reject({
        code: -10000
      })
    }

    let {
      loading = false, server = false
    } = options

    return new Promise((resolve, reject) => {
      uni.uploadFile({
        url: `${ baseUrl }${ url }`,
        name: 'picFile',
        filePath,
        header: getHeader('multipart/form-data', options),
        formData: {
          ...getCommonData(options),
          ...data
        },
        success (res) {
          if (loading !== false) {
            uni.hideLoading()
          }
          if (res.statusCode === 200) {
            let data = JSON.parse(res.data)
            if (data.code === 0) {
              resolve(data.data)
            } else {
              console.log('upload abnormal', url, data.code, data.msg)
              if (server && data.msg) {
                features.alert(data.msg)
              }
              reject(data)
            }
          } else {
            console.log('upload error', url, res.statusCode)
            reject(createHttpReject(res, options))
          }
        },
        fail (err) {
          console.log('upload fail', url, err.message)
          if (loading !== false) {
            uni.hideLoading()
          }
          reject(createSystemReject(err, options))
        }
      })
    })
  }
}

export default http
