<template>
  <div class="l-page">
    <div class="l-page__header--primary">
      <div class="l-page__title">{{ $t('tabbar.map') }}</div>
      <div class="l-flex__none l-flex--row">
        <img class="o-nav-icon" src="@/assets/icon-scan.png"/>
        <!-- <router-link
          tag="i"
          class="o-nav-icon"
          :to="{ name: 'settings' }">
          <van-icon name="setting-o" />
        </router-link> -->
      </div>
    </div>
    <div class="l-page__content l-flex--column">
      <div class="c-header has-padding--h has-hairline--b">
        <div class="c-header__wrapper" :style="{ transform: `translateY(${ translateY })` }">
          <div class="c-header__item">
            <div class="c-header__left">
              <div>{{ date }}</div>
              <div class="c-header__sp"></div>
              <div class="u-font">{{ openTime }}</div>
            </div>
            <div
              v-if="weather"
              class="c-header__right"
              @click="toWeather"
            >
              <v-resource class="c-header__icon" :info="weather"/>
              <div class="u-font--bold u-h-separate--list">{{ weather.temperature }}°C</div>
            </div>
          </div>
          <div
            v-for="(item, index) in scrolls"
            :key="index"
            class="c-header__item"
          >
            <div class="c-header__left">
              <template v-if="item.leftInfo">
                <v-resource class="c-header__icon" :info="item.leftInfo.icon"/>
                <div v-if="item.leftInfo.text" class="u-font--bold u-h-separate--list">{{ item.leftInfo.text.value }}</div>
              </template>
            </div>
            <div v-if="item.rightInfo && item.rightInfo.text" class="c-header__right">{{ item.rightInfo.text.value }}</div>
          </div>
        </div>
      </div>
      <div class="l-flex--column l-flex__fill u-relative">
        <div class="o-filter" :class="{ 'is-filter': isFilter }" @click="toFilter"></div>
        <van-tabs
          class="l-flex__fill c-nav-tabs"
          type='none'
          v-model="tabIndex"
          :swipe-threshold="5"
          :ellipsis="false"
          swipeable
          animated
          @change="switchTab"
        >
          <van-tab
            v-for="(tab, index) in tabs"
            :key="index"
          >
            <template #title>
              <div
                class="o-nav-tab"
                :class="{ 'is-selected': tabIndex === index }"
              >
                <div
                  v-if="tabIndex === index"
                  class="o-nav-tab__icon"
                  :style="{
                    'background-image': tab.info.picInfo.selectedCosUrl ? `url(${ tab.info.picInfo.selectedCosUrl })` : `url(${ baseUrl}${ tab.info.picInfo.selectedUrl })`
                  }"
                ></div>
                <div
                  v-else
                  class="o-nav-tab__icon"
                  :style="{
                    'background-image': tab.info.picInfo.unselectedCosUrl ? `url(${ tab.info.picInfo.unselectedCosUrl })` : `url(${ baseUrl}${ tab.info.picInfo.unselectedUrl })`
                  }"
                ></div>
                <div class="o-nav-tab__name" :class="{ 'u-color--blue': tabIndex === index, 'u-font--bold': tabIndex === index }">{{ tab.info.name }}</div>
              </div>
            </template>
            <div :ref="`tab${ index }`" class="has-padding--around">
              <div
                v-if="tab.loading && tab.total === 0"
                class="u-align-center u-separate--near"
              >
                <van-loading type="spinner"/>
              </div>
              <div
                v-if="!tab.loading && tab.total === 0 && tab.timestamp !== 0"
                class="o-empty">
                {{ $t('explore.empty') }}
              </div>
              <div
                v-if="tab.total > 0"
                class="c-list"
                :class="{ 'has-filter': tab.filter.length > 0 }"
              >
                <div
                  v-for="(item, index) in tab.list"
                  :key="index"
                  class="c-list__entity"
                  :class="{ 'is-animal': item.typeId === 3 }"
                >
                  <animal v-if="item.typeId === 3" :entity="item"/>
                  <facilities v-if="item.typeId === 8" :entity="item"/>
                  <activity v-if="item.typeId !== 3 && item.typeId !== 8" :entity="item" :now="now"/>
                </div>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
    <pop
      v-if="close2animal"
      ref="close2animal"
      mode="bottom"
      modal
      radius
      @close="clearClose2Animal"
    >
      <div class="c-list-title has-hairline--b">
        <i class="c-list-title__close" @click="closeClose2Animal"></i>{{ $t('explore.detail.close2animals') }}
      </div>
      <div class="c-animal-list">
        <v-animal
          v-for="(item, index) in close2animal"
          :key="index"
          class="c-vlist-item--far"
          :entity="item"
        />
      </div>
    </pop>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import { dateFormat, transformTime } from '@/utils/format.js'
import { sortBySortIndex } from '@/utils/sort.js'
import { Loading, Tab, Tabs } from 'vant'
import Pop from '@/components/Pop.vue'
import VAnimal from '@/components/map/VAnimal.vue'
import Animal from '@/components/map/Animal.vue'
import Activity from '@/components/map/Activity.vue'
import Facilities from '@/components/map/Facilities.vue'

export default {
  components: {
    [ Loading.name ]: Loading,
    [ Tabs.name ]: Tabs,
    [ Tab.name ]: Tab,
    Pop,
    VAnimal,
    Animal,
    Activity,
    Facilities
  },
  data () {
    return {
      localeKey: '',
      close2animal: null,
      loading: false,
      now: Date.now(),
      tabIndex: 0,
      scrollInto: '',
      translateY: '0',
      date: '',
      openTime: '',
      weather: null,
      scrolls: [],
      duration: 0,
      scrollIndex: 0
    }
  },
  computed: {
    ...mapState(['entityTypes']),
    ...mapGetters(['baseUrl']),
    totalScroll () {
      let total = this.scrolls.length
      if (this.date !== '' || this.weather !== null) {
        total += 1
      }
      return total
    },
    tabs () {
      return this.entityTypes.list
    },
    isFilter () {
      let tab = this.tabs[this.tabIndex]
      if (tab) {
        return tab.filter.length > 0
      }
      return false
    }
  },
  methods: {
    ...mapActions(['saveEntityTypes']),
    toWeather () {
      this.$router.push({ name: 'weather' })
    },
    toFilter () {
      this.$router.push({ name: 'filter' })
    },
    startScroll () {
      clearTimeout(this.$timer)
      this.translateY = `-${ this.scrollIndex }00%`
      if (this.duration > 0 && this.totalScroll > 1) {
        this.$timer = setTimeout(() => {
          this.scrollIndex = (this.scrollIndex + 1) % this.totalScroll
          this.startScroll()
        }, this.duration)
      }
    },
    checkScroll () {
      if (this.scrollIndex >= this.totalScroll) {
        this.scrollIndex = 0
      }
      this.startScroll()
    },
    refreshHeader () {
      this.$http.post('/api/common/park/detail').then(data => {
        if (data.parkOperatingHourList.length > 0) {
          let obj = data.parkOperatingHourList[0]
          this.date = dateFormat(obj.parkOpenTime, 'yyyy.MM.dd (w)')
          this.openTime = `${ transformTime(obj.parkOpenTime) }-${ transformTime(obj.parkCloseTime) }`
        }
        this.checkScroll()
      })

      this.$http.post('/api/common/weather/detail').then(data => {
        this.weather = {
          icon: data.picInfo.url,
          iconCos: data.picInfo.cosUrl,
          temperature: data.temperature
        }
        this.checkScroll()
      })

      this.$http.post('/api/common/information/list').then(data => {
        this.scrolls = data.data
        this.duration = data.duration * 1000
        this.checkScroll()
      })

      this.startScroll()
    },
    getTabs () {
      if (this.loading) {
        return
      }
      this.loading = true
      this.$http.post('/api/common/entity/sort/list').then(data => {
        this.loading = false
        if (this.entityTypes.timestamp !== data.updateTime) {
          let tabs = data.data.sort(sortBySortIndex).map(item => {
            return {
              id: `tab_${ item.id }_${ item.typeId }`,
              sortId: item.id,
              typeId: item.typeId,
              info: item,
              loading: false,
              timestamp: 0,
              filter: [],
              total: 0,
              list: []
            }
          })
          this.tabIndex = Math.min(this.tabIndex, tabs.length - 1)
          this.saveEntityTypes({
            list: tabs,
            timestamp: data.updateTime,
            index: this.tabIndex
          })
          this.switchTab(this.tabIndex)
        }
      }, () => this.loading = false)
    },
    getData () {
      let tab = this.tabs[this.tabIndex]
      if (!tab.loading) {
        tab.loading = true
        this.$http.post('/api/common/entity/list', {
          sortId: tab.sortId,
          filterList: tab.filter
        }).then(data => {
          tab.loading = false
          if (tab.timestamp !== data.updateTime) {
            tab.timestamp = data.updateTime
            tab.total = data.data.length
            tab.list = data.data.sort(sortBySortIndex)
          }
        }, () => tab.loading = false)
      }
    },
    toReset () {
      let tab = this.tabs[this.tabIndex]
      if (!tab.loading) {
        tab.timestamp = 0
        tab.filter = []
        tab.total = 0
        tab.list = []
        this.getData()
      }
    },
    switchTab (index) {
      this.tabIndex = index
      let tab = this.tabs[this.tabIndex]
      if (tab) {
        this.getData()
        this.scrollInto = tab.id
      } else {
        this.scrollInto = ''
      }
    },
    showClose2Animal (list) {
      this.close2animal = list.concat(list).concat(list)
      this.$nextTick(() => this.$refs.close2animal.open())
    },
    clearClose2Animal () {
      this.close2animal = null
    },
    closeClose2Animal () {
      if (this.close2animal !== null) {
        this.$refs.close2animal.close()
      }
    },
    refresh () {
      this.refreshHeader()
      this.getTabs()
    }
  },
  created () {
    this.$timer = -1
  },
  beforeDestroy () {
    clearTimeout(this.$timer)
  },
  activated () {
    this.$eventBus.$on('close2animal', this.showClose2Animal)

    if (this.localeKey !== this.$i18n.locale) {
      this.localeKey = this.$i18n.locale
      if (this.entityTypes) {
        this.entityTypes.timestamp = 0
      }
    }
    this.now = Date.now()
    this.refresh()
    if (this.tabs.length > 0) {
      this.tabIndex = this.entityTypes.index
      this.switchTab(this.tabIndex)
      this.tabs.forEach((tab, index) => {
        let dom = this.$refs[`tab${ index }`]
        if (dom && tab.scrollTop) {
          dom[0].parentNode.parentNode.scrollTop = tab.scrollTop
        }
        tab.scrollTop = 0
      })
    }
  },
  deactivated () {
    this.tabs.forEach((tab, index) => {
      let dom = this.$refs[`tab${ index }`]
      if (dom) {
        tab.scrollTop = dom[0].parentNode.parentNode.scrollTop
      }
    })
    this.$eventBus.$off('close2animal', this.showClose2Animal)
    clearInterval(this.$timer)
    this.entityTypes.index = this.tabIndex
    this.closeClose2Animal()
  }
}
</script>

<style lang="scss" scoped>
.c-header {
  position: relative;
  height: 96px;
  overflow: hidden;

  &__wrapper {
    height: 100%;
    transition: transform .2s;
    overflow: visible;
  }

  &__item {
    display: flex;
    align-items: center;
    height: 96px;
    font-size: 28px;
    color: $color--dark;
    line-height: 32px;

    & > * {
      flex: none;
    }
  }

  &__left {
    flex: 1;
    display: flex;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
  }

  &__right {
    flex: none;
    display: flex;
    align-items: center;
    padding-left: 12px;
  }

  &__sp {
    position: relative;
    margin: 0 8px;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      transform: translate(-50%, -50%);
      background-color: $color--dark;
    }
  }

  &__icon {
    width: 40px;
    height: 40px;
  }
}

.c-nav-tabs {
  display: flex;
  flex-direction: column;

  /deep/ .van-tabs__wrap {
    height: 180px;
    overflow: visible;
    z-index: 9;
  }

  /deep/ .van-tabs__wrap::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 240px;
    background: url(../../assets/tabs.png) 0 0 / 100% 100% no-repeat;
  }

  /deep/ .van-tabs__wrap::after {
    display: none;
  }

  /deep/ .van-tab {
    align-items: stretch;
    padding: 0;
    margin-left: 52px;

    &:first-child {
      margin-left: 48px;
    }

    &:last-child {
      margin-right: 48px;
    }
  }

  /deep/ .van-tabs__nav {
    background-color: transparent;
  }

  /deep/ .van-tabs__content {
    flex: 1;
  }

  /deep/ .van-tab__pane-wrapper {
    overflow-y: auto;
  }
}

.o-filter {
  position: absolute;
  top: 170px;
  left: 50%;
  width: 58px;
  height: 44px;
  transform: translateX(-50%);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-image: url(../../assets/icon-filters-normal.png);
  z-index: 99;

  &.is-filter {
    background-image: url(../../assets/icon-filters.png);
  }
}

.o-empty {
  font-size: 28px;
  color: $color--dark;
  line-height: 36px;
  text-align: center;
}

.c-list {
  display: grid;
  grid-template-columns: 315px 315px;
  grid-row-gap: 40px;
  justify-content: space-between;

  &__entity {
    grid-column-start: span 2;

    &.is-animal {
      grid-column-start: span 1;
    }
  }
}

.c-animal-list {
  box-sizing: border-box;
  max-height: 584px;
  padding: 40px 40px 24px;
  overflow-y: auto;
}
</style>
