<template>
  <div class="c-ticket">
    <div class="has-padding c-ticket__block">
      <div class="is-title--block is-ellipsis">{{ ticket.productName }}</div>
      <div class="c-ticket__note is-tip--dark has-shadow--text u-font u-separate--nearest">{{ ticket.ticketName }}</div>
      <div class="c-ticket__time is-content--orange is-ellipsis has-shadow--text u-font--bold u-separate">{{ validDate }}</div>
    </div>
    <template v-if="isShared">
      <toggle
        class="c-ticket__block"
        :title="ticket.ticketTypeName"
        :status="shareStatus"
        checked
        @toggle="onToggleChange"
      >
        <info
          type="shared"
          :msg="$t('mine.to')"
          :tip="sharedInfo.receiveName"
        />
      </toggle>
    </template>
    <template v-else>
      <template v-if="isExpired">
        <toggle
          class="c-ticket__block"
          :title="ticket.ticketTypeName"
          :msg="$t('mine.expired')"
          @toggle="onToggleChange"
        >
          <info :msg="$t('mine.expired')"/>
        </toggle>
      </template>
      <template v-else>
        <template v-if="isValid">
          <toggle
            class="c-ticket__block"
            :title="ticket.ticketTypeName"
            :status="shareStatus"
            checked
            @toggle="onToggleChange"
            @share="toShare"
          >
            <qr :qr="qrInfo"/>
          </toggle>
          <toggle
            v-for="(escheduler, index) in eschedulers"
            :key="index"
            class="c-ticket__block"
            :title="escheduler.name"
            :msg="escheduler.msg"
            :tip="escheduler.tip"
            :valid="escheduler.valid"
            @toggle="onToggleChange"
          >
            <info
              v-if="escheduler.status === 1"
              :msg="$t('mine.oping')"
              :tip="$t('mine.opingt')"
            />
            <qr
              v-if="escheduler.status === 2"
              :qr="escheduler.qr"
            />
            <info
              v-if="escheduler.status === 3"
              :msg="$t('mine.opf')"
            />
          </toggle>
          <div v-if="hasFastService" class="c-ticket__block is-fixed l-flex--row">
            <div class="l-flex--row">
              <div class="l-flex__fill">{{ $t('mine.fast') }}</div>
              <div class="l-flex__none">{{ fastServiceInfo.count }}/{{ fastServiceInfo.total }}</div>
            </div>
          </div>
          <div v-for="(escheduler, index) in fastESchedulers" :key="index" class="c-ticket__block is-fixed l-flex--row">
            <div class="l-flex--row">
              <div class="is-ellipsis l-flex__fill">{{ escheduler.rnaName }}</div>
              <div class="u-h-separate--near l-flex__none">{{ escheduler.timeSlot }}</div>
            </div>
          </div>
          <template v-if="hasT2P || hasJT2P">
            <div v-if="hasT2P" class="c-ticket__block is-fixed">
              <div class="is-tip--dark">{{ $t('mine.p2t') }}</div>
              <row
                v-if="t2p.time"
                class="u-separate--nearest"
                icon="time"
              >
                <div class="u-color--dark">{{ t2p.time }}</div>
              </row>
              <row
                v-if="t2p.place"
                class="u-separate--nearest"
                icon="location--primary"
              >
                <div class="u-color--blue">{{ t2p.place }}</div>
              </row>
            </div>
            <div v-if="hasJT2P" class="c-ticket__block is-fixed">
              <div class="is-tip--dark">{{ $t('mine.jp2t') }}</div>
              <row
                v-if="jt2p.time"
                class="u-separate--nearest"
                icon="time"
              >
                <div class="u-color--dark">{{ jt2p.time }}</div>
              </row>
              <row
                v-if="jt2p.place"
                class="u-separate--nearest"
                icon="location--primary"
              >
                <div class="u-color--blue">{{ jt2p.place }}</div>
              </row>
            </div>
            <div class="c-ticket__block is-fixed is-tip--dark u-font">
              {{ $t('mine.phone') }}<span class="u-color--blue" @click="toCall">(852) 3923 2323</span>
            </div>
          </template>
          <div v-if="canEscheduler && !hasEscheduler" class="c-ticket__block is-fixed">
            <div class="l-flex--row">
              <div class="l-flex__fill is-tip--dark u-font">{{ $t('mine.notq') }}</div>
              <div class="l-inline-flex l-flex__none" @click="toEscheduler">
                <div class="is-sub--blue">{{ $t('mine.tq') }}</div>
                <img class="o-icon" src="@/assets/icon-arrow--right--blue.png"/>
              </div>
            </div>
          </div>
        </template>
        <template v-else>
          <toggle
            :title="ticket.ticketTypeName"
            :msg="$t('mine.invalid')"
            @toggle="onToggleChange"
          >
            <info :msg="$t('mine.invalid')"/>
          </toggle>
        </template>
      </template>
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { dateFormat, transformTicketValidTime } from '@/utils/format.js'
import Row from '@/components/RowIcon.vue'
import Toggle from './Toggle.vue'
import Qr from './Qr.vue'
import Info from './Info.vue'

export default {
  components: {
    Row,
    Toggle,
    Qr,
    Info
  },
  props: {
    ticket: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      now: Date.now()
    }
  },
  computed: {
    ...mapGetters(['baseUrl']),
    isValid () {
      return this.ticket.isValid === 1
    },
    validDate () {
      return transformTicketValidTime(this.ticket)
    },
    isUsed () {
      return this.ticket.isUsed === 1
    },
    isExpired () {
      if (this.ticket.isExpire === 1) {
        return true
      }

      if (this.isValid && this.timeList && this.timeList.length > 0) {
        let now = Date.now()
        return this.timeList.findIndex(item => item.endTime > now) === -1
      }

      return false
    },
    qrInfo () {
      if (this.ticket.qrUrlList && this.ticket.qrUrlList[0]) {
        let qr = this.ticket.qrUrlList[0]
        return {
          id: this.ticket.ticketId,
          code: qr.qrcode,
          cosUrl: qr.picInfo && qr.picInfo.cosUrl || '',
          url: qr.picInfo && qr.picInfo.url || ''
        }
      }
      return { id: this.ticket.ticketId }
    },
    sharedInfo () {
      let index = (this.ticket.shareList || []).findIndex(item => item.shareStatus === 1 && item.status === 2)
      return index > -1 ? this.ticket.shareList[index] : null
    },
    isShared () {
      return this.sharedInfo !== null
    },
    canShare () {
      if (this.isShared || !this.isValid || this.ticket.isActive === 1 || this.isUsed || this.isExpired) {
        return false
      }
      return true
    },
    shareStatus () {
      if (this.isShared) {
        return 2
      }

      if (this.canShare) {
        if ((this.ticket.shareList || []).findIndex(item => item.shareStatus === 1 && item.status === 1) > -1) {
          return 1
        }
        return 0
      }

      return -1
    },
    canEscheduler () {
      return this.ticket.canEscheduler === true
    },
    eschedulers () {
      if (this.canEscheduler) {
        return (this.ticket.eschedulerList || []).map(this.transformEscheduler)
      }
      return []
    },
    hasEscheduler () {
      return this.eschedulers.length > 0
    },
    hasFastTrack () {
      return this.ticket.fastTrack ? true : false
    },
    fastESchedulers () {
      if (this.hasFastTrack) {
        return this.ticket.fastTrack.eschedulerList || []
      }
      return []
    },
    hasFastService () {
      return this.hasFastTrack && this.ticket.fastTrack.totalServiceCount > 0
    },
    fastServiceInfo () {
      return this.hasFastService ? {
        count: this.ticket.fastTrack.serviceCount || 0,
        total: this.ticket.fastTrack.totalServiceCount
      } : null
    },
    hasTimeAndPlace () {
      return this.hasFastTrack && this.ticket.fastTrack.meetingTimeAndPlace ? true : false
    },
    t2p () {
      let info = this.hasTimeAndPlace && this.ticket.fastTrack.meetingTimeAndPlace.timeAndPlace
      if (info && (info.time || info.place)) {
        return info
      }
      return null
    },
    hasT2P () {
      return this.t2p !== null
    },
    jt2p () {
      let info = this.hasTimeAndPlace && this.ticket.fastTrack.meetingTimeAndPlace.meetingTimeAndPlace
      if (info) {
        let time = info.time ? typeof info.time === 'string' ? info.time : info.time.join(', ') : ''
        if (time || info.place) {
          return {
            time: time,
            place: info.place
          }
        }
      }
      return null
    },
    hasJT2P () {
      return this.jt2p !== null
    }
  },
  methods: {
    onToggleChange (toggle) {
      this.$emit('toggle', toggle)
    },
    transformEscheduler (escheduler) {
      let { rnaName: name, status, timeSlot: tip, pairedCode, pairedCodePicInfo } = escheduler

      let qr = null
      if (pairedCode && pairedCodePicInfo) {
        qr = {
          code: pairedCode,
          cosUrl: pairedCodePicInfo.cosUrl || '',
          url: pairedCodePicInfo.url || ''
        }
      }

      if (status <= 1) {
        status = 1
        tip = this.$t('mine.tqing')
      }

      if (status === 3) {
        tip = this.$t('mine.tqf')
      }

      return {
        valid: status === 2,
        name, status, tip, qr,
        msg: dateFormat(escheduler.eschedulerTime, 'yyyy.MM.dd')
      }
    },
    toEscheduler () {
      this.$features.escheduler()
    },
    toShare () {
      if (this.canShare) {
        // this.$http.post('/api/common/user/ticket/share', {
        //   odTicketId: this.ticket.userTicketId
        // }, {
        //   login: true,
        //   loading: true,
        //   server: true
        // }).then(data => {
          this.$features.confirm({
            title: false,
            message: this.$t('mine.share'),
            btn: this.$t('mine.sharebtn')
          }).then(() => {
            console.log(this.ticket.userTicketId, this.ticket.productName)
          })
        // })
      }
    },
    toCall () {
      this.$features.phone('85239232323')
    }
  }
}
</script>

<style lang="scss" scoped>
.c-ticket {
  border-radius: 16px;
  background-color: $color-white;

  &__note {
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp: 2; /** 显示的行数 **/
    min-height: 72px;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  &__time {
    min-height: 40px;
  }

  &__block {
    &.is-fixed {
      padding: 32px 40px;
    }

    & + & {
      border-top: 1px dashed $color-separate;
    }
  }
}
</style>
