<template>
  <div class="c-toggle" :class="{ 'is-toggle': toggle }">
    <div class="l-flex--row" @click="toggle = !toggle">
      <div class="l-flex__auto">
        <div class="is-tip--dark">{{ title }}</div>
        <div v-if="note" class="is-tip u-font u-separate--gap">{{ note }}</div>
      </div>
      <i class="van-icon van-icon-arrow-down c-toggle__icon"></i>
    </div>
    <div v-if="toggle">
      <slot/>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant'

export default {
  components: {
    [ Icon.name ]: Icon
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    note: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      toggle: false
    }
  }
}
</script>

<style lang="scss" scoped>
.c-toggle {
  padding: 32px 40px;

  &.is-toggle {
    padding-bottom: 40px;

    .c-toggle__icon {
      transform: rotate(-180deg);
    }
  }

  &__icon {
    font-size: 32px;
    color: $color;
    transition: transform .3s;
  }
}
</style>
