<template>
  <div class="c-explore has-shadow" @click="toDetail">
    <v-resource class="c-explore__img" :info="entity.picInfo"/>
    <div class="c-explore__label is-ellipsis">{{ entity.name }}</div>
  </div>
</template>

<script>
export default {
  props: {
    entity: {
      type: Object,
      default: null
    }
  },
  methods: {
    toDetail () {
      this.$router.push({
        name: 'entity',
        params: {
          id: this.entity.id
        }
      })
    }
  }
}
</script>

