<template>
  <div v-if="has" class="c-footer has-padding has-hairline--t" @click.stop>
    <div v-if="hasAnimal" class="c-footer__button is-animal" @click="toBuyAnimalTicket"></div>
    <div v-if="canQueue" class="c-footer__button is-queue" @click="toQueue"></div>
    <div v-if="canOrder" class="c-footer__button is-order" @click="toOrder"></div>
    <div v-if="canBooking" class="c-footer__button is-book" @click="toBooking"></div>
    <div v-if="hasPhone" class="c-footer__button is-call" @click="toCall"></div>
  </div>
</template>

<script>
export default {
  props: {
    entity: {
      type: Object,
      default () {
        return null
      }
    }
  },
  computed: {
    relateList () {
      return this.entity.relateList || []
    },
    animalList () {
      let info = this.relateList.find(item => item.type === 'closeToAnimal')
      if (info) {
        return info.list
      }
      return null
    },
    hasAnimal () {
      return this.animalList !== null
    },
    bookingList () {
      let relation = this.relateList.find(item => item.type === 'booking')
      if (relation && relation.list) {
        return relation.list.filter(item => !!item.url)
      }
      return []
    },
    canBooking () {
      return this.bookingList.length > 0
    },
    queueList () {
      let relation = this.relateList.find(item => item.type === 'queue')
      if (relation && relation.list) {
        return relation.list.filter(item => !!item.url)
      }
      return []
    },
    canQueue () {
      return this.queueList.length > 0
    },
    orderList () {
      let relation = this.relateList.find(item => item.type === 'electronicOrder')
      if (relation && relation.list) {
        return relation.list.filter(item => !!item.url)
      }
      return []
    },
    canOrder () {
      return this.orderList.length > 0
    },
    phoneList () {
      return this.entity.phone ? this.entity.phone.filter(item => item !== '') : []
    },
    hasPhone () {
      return this.phoneList.length > 0
    },
    has () {
      return this.hasAnimal || this.canBooking || this.canQueue || this.canOrder || this.hasPhone
    }
  },
  methods: {
    toBuyAnimalTicket () {
      this.$eventBus.$emit('close2animal', this.animalList)
    },
    toBooking () {
      uni.navigateTo({
        url: `/pages/common/web/web?url=${ encodeURIComponent(this.bookingList[0].url) }`
      })
    },
    toQueue () {
      uni.navigateTo({
        url: `/pages/common/web/web?url=${ encodeURIComponent(this.queueList[0].url) }`
      })
    },
    toOrder () {
      uni.navigateTo({
        url: `/pages/common/web/web?url=${ encodeURIComponent(this.orderList[0].url) }`
      })
    },
    toCall () {
      this.$features.phone(this.phoneList[0])
    }
  }
}
</script>

