.c-footer {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;

  &__button {
    height: 36px;
    margin-left: 30px;
    background-position: 0 0;
    background-size: 100% 100%;
    background-repeat: no-repeat;

    &.is-animal {
      width: 298px;
      background-image: url(../../assets/btn-animal.png);
    }

    &.is-queue {
      width: 91px;
      background-image: url(../../assets/btn-queue.png);
    }

    &.is-order {
      width: 93px;
      background-image: url(../../assets/btn-order.png);
    }

    &.is-book {
      width: 80px;
      background-image: url(../../assets/btn-book.png);
    }

    &.is-call {
      width: 223px;
      background-image: url(../../assets/btn-call.png);
    }
  }
}
</style>
