<template>
  <list-page
    :title="$t('settings.account.email.title')"
    :note="$t('settings.account.email.note')"
    :button="$t('btn.next')"
    :disable="isDisable"
    @click="toConfirm"
  >
    <field
      v-model="email"
      :label="$t('settings.account.email.has')"
      :placeholder="$t('settings.account.email.placeholder')"
    />
  </list-page>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  name: 'SettingsAccountEmail',
  components: {
    ListPage,
    Field
  },
  data () {
    let user = this.$store.state.user
    return {
      email: user ? user.email : ''
    }
  },
  computed: {
    ...mapState(['user']),
    isDisable () {
      return !this.email
    }
  },
  methods: {
    ...mapActions(['setUserEmail']),
    toConfirm () {
      this.setUserEmail(this.email)
      this.$router.go(-1)
    }
  }
}
</script>

