<template>
  <div
    class="o-field"
    :class="{ 'is-error': isError, 'is-focused': isFocused }"
  >
    <div class="o-field__tip">{{ tipMsg }}</div>
    <input
      class="o-field__input"
      :type="type"
      :password="password"
      :placeholder="placeholder"
      :value="value"
      @focus="onFocus"
      @blur="onBlur"
      @input="onInput"
    />
    <div class="o-field__line"></div>
    <div class="o-field__note">{{ noteMsg }}</div>
  </div>
</template>

<script>
export default {
  name: 'Field',
  props: {
    type: {
      type: String,
      default: 'text'
    },
    value: {
      type: String,
      default: ''
    },
    password: {
      type: [Boolean, String],
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    labelMode: {
      type: String,
      default: 'auto'
    },
    label: {
      type: String,
      default: ''
    },
    note: {
      type: String,
      default: ''
    },
    errMsg: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      isFocused: false
    }
  },
  computed: {
    tipMsg () {
      switch (this.labelMode) {
        case 'none':
          return ''
        case 'auto':
          return this.value ? this.label || this.placeholder : ''
        default:
          return this.label
      }
    },
    isError () {
      return !!this.errMsg
    },
    noteMsg () {
      return this.errMsg || this.note
    }
  },
  methods: {
    onInput (e) {
      this.$emit('input', e.target.value)
    },
    onFocus () {
      this.isFocused = true
    },
    onBlur () {
      this.isFocused = false
    }
  }
}
</script>

