<template>
  <div v-if="show" class="c-pop" @touchmove.stop.prevent>
    <div class="c-pop__mask" :class="[{ 'is-show': aniMaskShow }, modeClass]" @click.stop="modalClose">
      <div class="c-pop__content" :class="{ 'u-background--white': !opacity, 'has-radius': radius }" @click.stop="contentClick">
        <slot/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    mode: {
      type: String,
      default: 'pop'
    },
    modal: {
      type: [Boolean, String],
      default: false
    },
    clickClose: {
      type: [Boolean, String],
      default: false
    },
    opacity: {
      type: [Boolean, String],
      default: false
    },
    radius: {
      type: [Boolean, String],
      default: false
    }
  },
  data () {
    return {
      show: false,
      aniMaskShow: false,
    }
  },
  computed: {
    modeClass () {
      return `is-${ this.mode }`
    }
  },
  methods: {
    open () {
      this.show = true
      this.$nextTick(() => {
        setTimeout(()=>{
          this.aniMaskShow = true
        }, 50)
      })
    },
    close (cb) {
      if (this.show && this.aniMaskShow) {
        this.aniMaskShow = false
        this.$nextTick(() => {
          setTimeout(() => {
            this.show = false
            cb && cb()
            this.$emit('close')
          }, 300)
        })
      }
    },
    modalClose () {
      if (this.clickClose || this.modal) {
        this.close()
      }
    },
    contentClick () {
      if (this.clickClose) {
        this.close()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.c-pop {
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;

  &__mask {
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    background-color: rgba($color-black, .4);
    transition-property: opacity;
    transition-duration: .3s;
    opacity: 0;

    &.is-pop {
      display: flex;
      justify-content: center;
      align-items: center;

      .c-pop__content {
        border-radius: 8px;
      }
    }

    &.is-bottom {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;


      .c-pop__content {
        transition-duration: .3s;
        transform: translateY(100%);
        transition-property: transform;

        &.has-radius {
          border-radius: 16px 16px 0 0;
        }
      }
    }

    &.is-show {
      opacity: 1;

      &.is-bottom {
        .c-pop__content {
          transform: translateY(0);
        }
      }
    }
  }
}
</style>
