import Vue from 'vue'
import Vuex from 'vuex'
import i18n from '@/i18n/index.js'
import { info } from '@/utils/auth.js'

Vue.use(Vuex)

const state = {
  authInfo: info(),
  config: JSON.parse(window.localStorage.getItem('config')) || {
    manageApiServerUrl: 'https://wcminiprogramcms.oceanpark.com.hk/admin',
    sharePic: '',
    avatar: '',
    picLoopTime: 3,
    maxEschedulerCount: 3,
    orderTimeoutLong: 15,
    orderTimeList: [],
    afterOrderTimeList: []
  },
  entityTypes: {
    timestamp: 0,
    index: 0,
    list: []
  },
  countryList: [],
  orderTickets: null,
  eschedulerTickets: null,
  userMails: [],
  parkMails: []
}

const getters = {
  isAuth (state) {
    return state.authInfo !== null
  },
  isAbnormal (state, getters) {
    return getters.isAuth && state.authInfo.status === void 0
  },
  isLogined (state, getters) {
    return getters.isAuth && state.authInfo.type > 0
  },
  isRegistered (state, getters) {
    return getters.isAuth && state.authInfo.status === 2
  },
  baseUrl (state) {
    return state.config.manageApiServerUrl
  },
  loopInterval (state) {
    return Math.max(1, state.config.picLoopTime) * 1000
  },
  loopDuration () {
    return 500
  },
  orderTimeout (state) {
    return state.config.orderTimeoutLong * 60 * 1000
  },
  nickName (state, getters) {
    return getters.isAuth && state.authInfo.nickName || i18n.t('common.visitor')
  },
  avatarUrl (state, getters) {
    if (getters.isAuth) {
      if (state.authInfo.avatarCosUrl) {
        return state.authInfo.avatarCosUrl
      }
      if (getters.baseUrl && state.authInfo.avatarUrl) {
        return `${ getters.baseUrl }${ state.authInfo.avatarUrl }`
      }
    }
    return ''
  },
  parkPopMails (state) {
    return state.parkMails.filter(mail => mail.origin.showType === 1 && mail.isFresh)
  },
  hasParkMails (state, getters) {
    return getters.parkPopMails.length > 0
  }
}

const mutations = {
  config (state, payload) {
    state.config = payload
    window.localStorage.setItem('config', JSON.stringify(payload))
  },
  authInfo (state, payload) {
    if (payload) {
      state.authInfo = payload
      window.localStorage.setItem('authInfo', JSON.stringify(payload))
    } else {
      state.authInfo = null
      window.localStorage.removeItem('authInfo')
    }
  },
  entityTypes (state, payload) {
    state.entityTypes = payload
  },
  countryList (state, payload) {
    state.countryList = payload
  },
  orderTickets (state, payload) {
    state.orderTickets = payload
  },
  eschedulerTickets (state, payload) {
    state.eschedulerTickets = payload
  },
  userMails (state, payload) {
    state.userMails = payload
  },
  parkMails (state, payload) {
    state.parkMails = payload
  }
}

const actions = {
  setAuthInfo ({ commit }, payload) {
    commit('authInfo', payload)
  },
  saveEntityTypes ({ commit }, payload) {
    commit('entityTypes', payload)
  },
  saveOrderTickets ({ commit }, payload) {
    commit('orderTickets', payload)
  },
  saveESchedulerTickets ({ commit }, payload) {
    commit('eschedulerTickets', payload)
  }
}

let store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  modules: {}
})

export default store
