<template>
  <list-page
    :title="$t('settings.account.phone.title')"
    :note="$t('settings.account.phone.note')"
    :button="$t('btn.next')"
    :disable="isDisable"
    @click="toConfirm"
  >
    <phone
      v-model="phone"
      :area-code="areaCode"
      @change="onAreaCodeChange"
    />
  </list-page>
</template>

<script>
import { mapActions } from 'vuex'
import ListPage from '@/components/list/ListPage.vue'
import Phone from '@/components/list/Phone.vue'

export default {
  name: 'SettingsAccountPhone',
  components: {
    ListPage,
    Phone
  },
  data () {
    let user = this.$store.state.user
    return {
      areaCode: user ? user.areaCode : '',
      phone: user ? user.phone : ''
    }
  },
  computed: {
    isDisable () {
      return !this.areaCode || !this.phone
    }
  },
  methods: {
    ...mapActions(['setUserAreaCode', 'setUserPhone']),
    onAreaCodeChange (areaCode) {
      this.areaCode = areaCode
    },
    toConfirm () {
      this.setUserAreaCode(this.areaCode)
      this.setUserPhone(this.phone)
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
