<template>
  <list-page
    :title="$t('settings.account.pwd.title')"
    :note="$t('settings.account.pwd.note')"
    :button="$t('btn.next')"
    :disable="isDisable"
    @click="toConfirm"
  >
    <field
      password
      v-model="pwd"
      :placeholder="$t('settings.account.pwd.curr')"
      :err-msg="pwdErr"
    />
    <field
      password
      v-model="npwd"
      :placeholder="$t('settings.account.pwd.npwd')"
      :err-msg="npwdErr"
    />
    <field
      password
      v-model="rpwd"
      :placeholder="$t('settings.account.pwd.rpwd')"
      :err-msg="rpwdErr"
    />
  </list-page>
</template>

<script>
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'
import { checkPwd } from '@/utils/patt.js'

export default {
  name: 'SettingsAccountPwd',
  components: {
    ListPage,
    Field
  },
  data () {
    return {
      pwd: '',
      pwdErr: '',
      npwd: '',
      npwdErr: '',
      rpwd: '',
      rpwdErr: '',
    }
  },
  computed: {
    isDisable () {
      return !this.pwd || !this.npwd || !this.rpwd
    }
  },
  methods: {
    toConfirm () {
      if (this.pwd !== '111111') {
        this.pwdErr = this.$t('errMsg.pwd.incorrect')
      } else {
        this.pwdErr = ''
      }

      let npwdCheck = checkPwd(this.npwd)
      if (npwdCheck.result) {
        this.npwdErr = ''
      } else {
        this.npwdErr = this.$t(`errMsg.pwd.${ npwdCheck.reason }`)
      }

      let rpwdCheck = checkPwd(this.rpwd)
      if (rpwdCheck.result) {
        if (this.rpwd === this.npwd) {
          this.rpwdErr = ''
        } else {
          this.rpwdErr = this.$t('errMsg.pwd.mismatch')
        }
      } else {
        this.rpwdErr = this.$t(`errMsg.pwd.${ rpwdCheck.reason }`)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
