import store from '@/store/index.js'
import http from './http.js'
import EventBus from './event-bus.js'

let timer = -1

let authInfo = window.localStorage.getItem('authInfo') || null
if (authInfo) {
  authInfo = JSON.parse(authInfo)
  if (!authInfo.tokenExpire || authInfo.tokenExpire <= Date.now()) {
    console.log('Clear authorization')
    authInfo = null
    window.localStorage.removeItem('authInfo')
  }
}

function save (data) {
  if (authInfo && data && data.type < authInfo.type) {
    return
  }

  authInfo = data
  store.commit('authInfo', authInfo)
  retry(3600000)
}

function retry (time = 0, cb) {
  clearTimeout(timer)
  if (!authInfo || authInfo.type === 0) {
    setTimeout(() => authNomral(cb), time)
  }
}

let fetching = false
export function authNomral (cb) {
  if (fetching) {
    return
  }

  // save({
  //   type: 1,
  //   userIdType: 2,
  //   userId: 10000131,
  //   token: 'mp_566a247c-782b-4409-956b-8b0b911f5a67mrx6VXTVgpBwNdHrMdGa9uNyWY9D',
  //   tokenExpire: 1591148392246
  // })

  fetching = true
  http.post('/api/common/user/token', {
    deviceId: 'app-device'
  }, { auth: false }).then(data => {
    save({
      type: 0,
      token: data.token,
      tokenExpire: data.tokenExpire
    })
    cb && cb()
  }, () => {
    retry(2000, cb)
  }).finally(() => fetching = false)
}

export function info () {
  return authInfo
}

export function isExpired (time = 600000) {
  if (!authInfo) {
    console.log('Unauthorized')
    return true
  }

  let expire = authInfo.tokenExpire
  if (expire <= Date.now()) {
    console.log('Token is expired')
    save('authInfo', null)
    return true
  }

  // 提前10分钟获取
  if (expire <= Date.now() + time) {
    console.log('Token will expire', time)
    retry()
  }

  return false
}
