<template>
  <div v-if="qr" class="c-qr">
    <template v-if="qr.code">
      <v-resource class="c-qr__img" :info="qr"/>
      <div class="u-separate--nearest">{{ qr.code }}</div>
    </template>
    <div v-if="qr.id" class="l-inline-flex is-sub u-font--bold u-separate" @click="toTicketDetail">
      {{ $t('mine.detail') }}<img class="c-qr__icon u-h-separate--gap" src="@/assets/icon-info.png"/>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: {
    qr: {
      type: Object,
      default: null
    }
  },
  computed: {
    ...mapGetters(['baseUrl']),
    url () {
      return this.qr ? this.qr.cosUrl || (this.baseUrl && this.qr.url ? `${ this.baseUrl }${ this.qr.url }` : '') : ''
    }
  },
  methods: {
    toTicketDetail () {
      uni.navigateTo({
        url: `/pages/ticket/detail/detail?id=${ this.qr.id }`
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.c-qr {
  padding: 18px 0 8px;
  font-size: 24px;
  color: $color--dark;
  line-height: 29px;
  text-align: center;
  @include u-font;

  &__img {
    width: 230px;
    height: 230px;
  }

  &__icon {
    width: 28px;
    height: 28px;
  }
}
</style>
