<template>
  <div id="app">
    <transition :name="transition">
      <keep-alive>
        <router-view v-if="$route.meta.tabbar" />
      </keep-alive>
    </transition>
    <transition :name="transition">
      <router-view v-if="!$route.meta.tabbar" />
    </transition>
  </div>
</template>

<script>
export default {
  data () {
    return {
      transition: 'slide-r2l'
    }
  },
  computed: {
    key () {
      return this.$route.fullPath
    }
  },
  methods: {
    checkDirection (to, from) {
      if (this.$back) {
        this.$back = false
        this.transition = 'slide-l2r'
      } else {
        if (to.meta.group && from.meta.group && to.meta.group === from.meta.group) {
          this.transition = to.meta.depth < from.meta.depth ? 'slide-l2r' : 'slide-r2l'
        } else if (to.meta.tabbar) {
          this.transition = 'slide-l2r'
        } else {
          this.transition = 'slide-r2l'
        }
      }
    },
    back () {
      this.$back = true
      this.$router.go(-1)
    }
  },
  beforeMount () {
    let element = document.getElementById('loading')
    if (element) {
      document.body.removeChild(element)
    }

    this.$router.beforeEach((to, from, next) => {
      if (to.matched.length === 0) {
        this.transition = 'slide-r2l'
        this.$nextTick(() => {
          next({
            name: 'home'
          })
        })
      } else {
        this.checkDirection(to, from)
        this.$nextTick(next)
      }
    })

    this.$eventBus.$on('back', this.back)
  }
}
</script>

<style lang="scss">
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
</style>
