<template>
  <list-page :title="$t('settings.language')">
    <cell
      v-for="lang in langs"
      :key="lang.key"
      :title="lang.text"
      @click="change(lang.key)"
    >
      <img
        v-if="lang.key === langKey"
        class="o-hook"
        src="@/assets/icon-hook.png"
      />
    </cell>
  </list-page>
</template>

<script>
import ListPage from '@/components/list/ListPage.vue'
import Cell from '@/components/list/Cell.vue'
import { langs, changeLang } from '@/i18n/index.js'

export default {
  components: {
    ListPage,
    Cell
  },
  data () {
    return {
      langKey: this.$i18n.locale,
      langs
    }
  },
  computed: {
    lang () {
      let langKey = this.$i18n.locale
      let lang = langs.find(lang => lang.key === langKey)
      return lang ? lang.text : ''
    }
  },
  methods: {
    change (langKey) {
      if (changeLang(langKey)) {
        this.langKey = langKey
        this.$router.go(-1)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.o-hook {
  width: 40px;
  height: 40px;
}
</style>
