<template>
  <div class="l-page">
    <v-nav normal/>
    <div class="l-page__content c-list-page">
      <div
        class="c-list-page__header"
        :class="{ 'has-note': hasNote }"
      >
        <div class="c-list-page__title">{{ title }}</div>
        <div
          v-if="hasNote"
          class="c-list-page__note"
        >{{ note }}</div>
      </div>
      <slot/>
      <div
        v-if="hasBtn"
        class="c-list-page__footer"
      >
        <v-button
          :text="button"
          :disable="disable"
          v-on="$listeners"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant'
import VButton from '@/components/VButton.vue'

export default {
  name: 'ListPage',
  components: {
    [ Icon.name ]: Icon,
    VButton
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    note: {
      type: String,
      default: ''
    },
    button: {
      type: String,
      default: ''
    },
    disable: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    hasNote () {
      return !!this.note
    },
    hasBtn () {
      return !!this.button
    }
  }
}
</script>

<style lang="scss" scoped>
.c-list-page {
  padding: 53px 75px 20px;

  &__header {
    margin-bottom: 88px;

    &.has-note {
      margin-bottom: 28px;
    }
  }

  &__title {
    font-size: 60px;
    color: $color--dark;
    line-height: 72px;
  }

  &__note {
    margin-top: 40px;
    font-size: 24px;
    color: $color--dark;
    line-height: 36px;
    @include u-font;
  }

  &__footer {
    margin-top: 48px;
  }
}
</style>
