<template>
  <div
    class="o-button"
    :class="{ 'is-disable': disable }"
    @click="onClick"
  >{{ text }}</div>
</template>

<script>
export default {
  props: {
    disable: {
      type: [Boolean, String],
      default: false
    },
    text: {
      type: String,
      default: ''
    }
  },
  methods: {
    onClick () {
      if (!this.disable) {
        this.$emit('click')
      }
    }
  }
}
</script>

