import store from '@/store/index.js'
import http from './http.js'
import EventBus from './event-bus.js'
import features from './features.js'
import { dateFormat } from './format.js'
import { sortBySortIndex } from './sort.js'

function necessary (url, data, options, cb) {
  http.post(url, data, options).then(cb, () => setTimeout(() => necessary(url, data, options, cb), 2000))
}

function config () {
  necessary('/api/common/config', {}, {}, cfg => {
    store.commit('config', {
      manageApiServerUrl: cfg.manageApiServerUrl,
      picLoopTime: cfg.picLoopTime,
      avatar: cfg.defaultPics[0] && cfg.defaultPics[0].url || '',
      sharePic: cfg.defaultPics[2] && cfg.defaultPics[2].url || '',
      maxEschedulerCount: cfg.maxEschedulerCount || 3,
      orderTimeoutLong: cfg.orderTimeoutLong || 15,
      orderTimeList: cfg.orderTimeList || [],
      afterOrderTimeList: cfg.afterOrderTimeList || []
    })
  })
}

function country () {
  necessary('/api/common/country/list', {}, {}, data => {
    store.commit('countryList', data.countryList)
  })
}

function transformMail (mail, type) {
  return {
    type,
    id: mail.msgId,
    time: dateFormat(mail.addTime, 'yyyy.MM.dd hh:mm:ss'),
    isFresh: mail.isRead === 0,
    origin: mail
  }
}

const mailCfg = {
  user: {
    type: 1,
    loading: false,
    timestamp: -1,
    key: 'userMails',
    event: 'usermail',
    check: false
  },
  park: {
    type: 2,
    loading: false,
    timestamp: -1,
    key: 'parkMails',
    event: 'sysmail',
    check: false
  }
}

function mail (options, check = false) {
  options.check = check

  if (options.loading) {
    return
  }

  options.loading = true

  necessary('/api/common/user/msg/list', {
    msgType: options.type
  }, {
    login: true
  }, data => {
    options.loading = false
    if (data.updateTime !== options.timestamp) {
      options.timestamp = data.updateTime
      store.commit(options.key, data.data.sort(sortBySortIndex).map(mail => transformMail(mail, options.type)))
      EventBus.$emit(options.event)
      if (options.check && options.type === 2) {
        features.checkMails()
      }
    }
  })
}

export function initMails (all = true) {
  if (all) {
    mail(mailCfg.user)
    mail(mailCfg.park)
  } else {
    mail(mailCfg.park, true)
  }
}

export function constant () {
  config()
  country()
}
