<template>
  <transition :name="direction">
    <keep-alive :include="include">
      <router-view :key="$route.meta.depth" />
    </keep-alive>
  </transition>
</template>

<script>
export default {
  name: 'KeepLiveWrapper',
  data () {
    return {
      direction: 'slide-r2l',
      routeChain: []
    }
  },
  computed: {
    include () {
      return this.routeChain.map(route => route.component)
    }
  },
  methods: {
    addRouteChain (route) {
      let depth = route.meta.depth
      if (this.routeChain.length > 0) {
        let last = this.routeChain.length - 1
        while (last >= 0) {
          if (depth <= this.routeChain[last].depth) {
            this.routeChain.pop()
            last -= 1
          } else {
            break
          }
        }
      }
      this.routeChain.push({
        path: route.fullPath,
        component: route.matched[route.matched.length - 1].components.default.name,
        depth: depth || 0
      })
      this.$log('KeepLiveWrapper', this.include)
    },
    checkDirection (to, from) {
      this.addRouteChain(to)
      this.direction = to.meta.depth < from.meta.depth ? 'slide-l2r' : 'slide-r2l'
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.addRouteChain(to)
    })
  },
  beforeRouteUpdate (to, from, next) {
    this.checkDirection(to, from)
    next()
  }
}
</script>

