<template>
  <div class="l-page">
    <v-nav
      :title="$t('explore.filter.title')"
      icon="cross"
    >
      <span class="is-content u-color--white" @click="toReset">{{ $t('explore.filter.clear') }}</span>
    </v-nav>
    <div class="l-page__content l-flex--column">
      <van-tabs
        class="l-flex__none c-nav-tabs"
        type='none'
        v-model="tabIndex"
        swipeable
        :swipe-threshold="5"
        :ellipsis="false"
        animated
        @change="getFilters"
      >
        <van-tab
          v-for="(tab, index) in tabs"
          :key="index"
        >
          <template #title>
            <div
              class="o-nav-tab"
              :class="{ 'is-selected': tabIndex === index }"
            >
              <div
                v-if="tabIndex === index"
                class="o-nav-tab__icon"
                :style="{
                  'background-image': tab.info.picInfo.selectedCosUrl ? `url(${ tab.info.picInfo.selectedCosUrl })` : `url(${ baseUrl}${ tab.info.picInfo.selectedUrl })`
                }"
              ></div>
              <div
                v-else
                class="o-nav-tab__icon"
                :style="{
                  'background-image': tab.info.picInfo.unselectedCosUrl ? `url(${ tab.info.picInfo.unselectedCosUrl })` : `url(${ baseUrl}${ tab.info.picInfo.unselectedUrl })`
                }"
              ></div>
              <div class="o-nav-tab__name" :class="{ 'u-color--blue': tabIndex === index, 'u-font--bold': tabIndex === index }">{{ tab.info.name }}</div>
            </div>
          </template>
        </van-tab>
      </van-tabs>
      <div class="l-flex__fill u-scroll-y">
        <div v-for="(block, index) in selectedBlocks" :key="index" class="c-block has-padding--around">
          <div v-for="(item, oindex) in block.list" :key="oindex" class="c-vlist-item--farther">
            <template v-if="item.valueType === 'multiSelect'">
              <div class="l-flex--row">
                <div class="is-tip--dark l-flex__fill">{{ item.name }}</div>
                <div v-if="item.canToggle" class="o-toggle l-flex__none" @click="toggleBlock(item)">
                  {{ item.toggle ? $t('explore.filter.less') : $t('explore.filter.more') }}
                  <i class="o-toggle__icon is-small" :class="{ 'is-toggle': item.toggle }"></i>
                </div>
              </div>
              <div class="c-options u-separate--gap">
                <div v-for="option in item.pre" :key="option.id" class="c-options__item o-option" :class="{ 'is-selected': option.selected }" @click="selectChange(option)">{{ option.name }}</div>
                <template v-if="item.toggle">
                  <div v-for="option in item.next" :key="option.id" class="c-options__item o-option" :class="{ 'is-selected': option.selected }" @click="selectChange(option)">{{ option.name }}</div>
                </template>
              </div>
            </template>
            <template v-if="item.valueType === 'range'">
              <div class="l-flex--row">
                <div class="is-tip--dark l-flex__fill">{{ item.name }}</div>
                <div class="is-tip--blue u-font--bold l-flex__nonw">{{ item.value }}</div>
                <div class="is-tip--blue u-font--bold l-flex__nonw">{{ item.unit }}</div>
              </div>
              <van-slider
                class="u-separate"
                v-model="item.value"
                :min="item.min"
                :max="item.max"
                :step="item.step"
                active-color="#00A9E0"
                inactive-color="#DDDDDD"
              />
            </template>
            <div v-if="item.valueType === 'checkBox'" class="is-tip--dark u-relative">
              {{ item.name }}
              <van-switch
                class="is-fixed-r"
                :data-key="item.id"
                v-model="item.value"
                active-color="#00A9E0"
                inactive-color="#DDDDDD"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="c-fixed-footer">
      <div class="o-button is-trigger is-auto" @click="toBack">{{ $t('explore.filter.check') }}</div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { Loading, Tab, Tabs, Slider, Switch } from 'vant'
import { sortByIndex } from '@/utils/sort.js'

export default {
  components: {
    [ Loading.name ]: Loading,
    [ Tabs.name ]: Tabs,
    [ Tab.name ]: Tab,
    [ Slider.name ]: Slider,
    [ Switch.name ]: Switch
  },
  data () {
    return {
      blocks: [],
      filterList: {},
      scrollInto: '',
      tabIndex: 0
    }
  },
  computed: {
    ...mapState(['entityTypes']),
    ...mapGetters(['baseUrl']),
    tabs () {
      return this.entityTypes.list
    },
    selectedTab () {
      return this.tabs.length > 0 ? this.tabs[this.tabIndex] : null
    },
    selectedFilters () {
      if (this.selectedTab) {
        return this.filterList[this.selectedTab.id] || []
      }
      return []
    },
    selectedBlocks () {
      if (this.selectedFilters.length > 0) {
        let blocks = []
        let type = ''
        let list
        this.selectedFilters.forEach(item => {
          if (type !== item.valueType) {
            list = []
            type = item.valueType
            blocks.push({
              type, list
            })
          }
          list.push(item)
        })
        return blocks
      }
      return []
    }
  },
  methods: {
    isValid (item) {
      switch (item.valueType) {
        case 'multiSelect':
          return item.value !== void 0 && item.value.length > 0
        case 'range':
          return typeof item.defaultValue === 'number'
        case 'checkBox':
          return typeof item.defaultValue === 'boolean'
        default:
          return false
      }
    },
    transform (item) {
      let value
      switch (item.valueType) {
        case 'multiSelect':
          value = (item.value || []).map(info => {
            return {
              id: info.id,
              name: info.name,
              selected: false
            }
          })
          return {
            id: item.id,
            valueType: item.valueType,
            name: item.name,
            canToggle: value.length > 8,
            toggle: false,
            value: value,
            pre: value.slice(0, 8),
            next: value.slice(8)
          }
        case 'range':
        case 'checkBox':
          item.value = item.defaultValue
          break
        default:
          break
      }
      return item
    },
    initFilters (filterList, tab) {
      if (tab.filter.length > 0) {
        filterList.forEach(filter => {
          let temp = tab.filter.find(item => item.id === filter.id)
          if (temp) {
            switch (filter.valueType) {
              case 'multiSelect':
                filter.value.forEach(block => {
                  if (temp.value.findIndex(id => id === block.id) >= 0) {
                    block.selected = true
                  }
                })
                break
              case 'range':
              case 'checkBox':
                filter.value = temp.value
                break
              default:
                break
            }
          }
        })
      }
    },
    getFilters () {
      if (this.selectedFilters.length === 0) {
        this.$http.post('/api/common/entity/sort/detail', {
          sortId: this.selectedTab.sortId
        }, {
          loading: true
        }).then(data => {
          let filterList = data.filterList.filter(item => this.isValid(item)).sort(sortByIndex).map(this.transform)
          this.initFilters(filterList, this.selectedTab)
          this.$set(this.filterList, this.selectedTab.id, filterList)
        })
      } else {
        this.initFilters(this.selectedFilters, this.selectedTab)
      }
    },
    toggleBlock (item) {
      item.toggle = !item.toggle
    },
    selectChange (option) {
      option.selected = !option.selected
    },
    filter (item) {
      switch (item.valueType) {
        case 'multiSelect':
          return item.value.findIndex(option => option.selected) >= 0
        case 'range':
        case 'checkBox':
          return item.value !== item.defaultValue
        default:
          return true
      }
    },
    transformFilter (item) {
      switch (item.valueType) {
        case 'multiSelect':
          return {
            id: item.id,
            value: item.value.filter(option => option.selected).map(option => option.id)
          }
        case 'range':
          return {
            id: item.id,
            value: item.value
          }
        case 'checkBox':
          return {
            id: item.id,
            value: item.value
          }
        default:
          break
      }
      return item
    },
    toReset () {
      this.selectedFilters.map(item => {
        switch (item.valueType) {
          case 'multiSelect':
            item.value.forEach(option => option.selected = false)
            break
          case 'range':
          case 'checkBox':
            item.value = item.defaultValue
            break
          default:
            break
        }
      })
    },
    toBack () {
      let tab = this.selectedTab
      if (tab) {
        console.log(this.selectedFilters.filter(item => this.filter(item)).map(this.transformFilter))
        // this.entityTypes.index = this.tabIndex
        // tab.filter = this.selectedFilters.filter(item => this.filter(item)).map(this.transformFilter)
        // tab.loading = false
        // tab.timestamp = 0
        // tab.total = 0
        // tab.list = []
      }
      // this.$features.explore()
    }
  },
  created () {
    if (this.entityTypes && this.entityTypes.list.length > 0) {
      this.tabIndex = this.entityTypes.index
      this.getFilters()
    }
  }
}
</script>

<style lang="scss" scoped>
.c-nav-tabs {
  display: flex;
  flex-direction: column;

  /deep/ .van-tabs__wrap {
    height: 180px;
    overflow: visible;
    z-index: 9;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0 , .1);
  }

  /deep/ .van-tabs__wrap::after {
    display: none;
  }

  /deep/ .van-tab {
    align-items: stretch;
    padding: 0;
    margin-left: 52px;

    &:first-child {
      margin-left: 48px;
    }

    &:last-child {
      margin-right: 48px;
    }
  }

  /deep/ .van-tabs__nav {
    background-color: transparent;
  }

  /deep/ .van-tabs__content {
    display: none;
  }
}

.c-footer-icon {
  margin-right: 20px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  border: 2px solid $color-blue;
  border-radius: 48px;
}

.c-block {
  & + & {
    border-top: 24px solid $color-separate--block;
  }
}

.o-toggle {
  &__icon {
    background-image: url(~@/assets/icon-add.png);

    &.is-toggle {
      background-image: url(~@/assets/icon-minus.png);
    }
  }
}
</style>
